import React, { useState } from 'react'
import { useForm } from 'react-hook-form'
import styles from './styles.module.scss'
import { Product as ProductModel } from 'models/product';
import Button from 'components/common/Button/Button';
import Input from 'components/common/Input/Input';
import ErrorMessage from 'components/common/Messages/ValidateMessage';
import { ReduceReturnType } from '../Storage'

const validation = {
  name: {
    required: true,
    maxLength: 40,
    minLength: 2,
  },
  category: {
    required: true,
    maxLength: 20,
    minLength: 2,
  },
  quantity: {
    required: true,
    pattern: /[0-9]/g
  },
  price: {
    required: true,
    pattern: /[0-9]/g
  }
}

interface DefaultValuesProps {
  _id?: string
  defaultName?: string
  defaultCategory?: string
  defaultQuantity?: number
  defaultUnit?: string
  defaultPrice?: number
}

interface AddProductFormProps extends DefaultValuesProps {
  categoryList: ReduceReturnType | undefined
  formTitle: string
  message?: JSX.Element | boolean | null
  handleSubmitForm: (data: ProductModel) => void
}

const ProductForm: React.FC<AddProductFormProps> = ({
  defaultName,
  defaultCategory,
  defaultQuantity,
  defaultUnit,
  defaultPrice,
  categoryList,
  formTitle,
  message,
  handleSubmitForm
}) => {

  const [categorySwitch, setCategorySwitch] = useState(true)
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<ProductModel>();

  const nameErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Nazwa jest wymagana'} />
      case 'minLength':
        return <ErrorMessage message={'Nazwa musi zawiera conajmiej 2 znaki'} />
      case 'maxLength':
        return <ErrorMessage message={'Nazwa moe zawiera maksymalnie 20 znakw'} />
      default:
        return null
    }
  }

  const categoryErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Kategoria jest wymagana'} />
      case 'minLength':
        return <ErrorMessage message={'Kategoria musi zawiera conajmiej 2 znaki'} />
      case 'maxLength':
        return <ErrorMessage message={'Kategoria moe zawiera maksymalnie 20 znakw'} />
      default:
        return null
    }
  }

  const quantityErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Stan jest wymagany'} />
      case 'pattern':
        return <ErrorMessage message={'Stan jest niepoprawny'} />
      default:
        return null
    }
  }

  const priceErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Haso jest wymagane'} />
      case 'pattern':
        return <ErrorMessage message={'Haso musi zawiera: co najmniej 8 znakw, 1 cyfr, 1 ma liter i 1 wielk liter'} />
      default:
        return null
    }
  }

  return (
    <div className={styles.productForm}>
      <h2>{formTitle}</h2>
      <form onSubmit={handleSubmit(handleSubmitForm)}>
        <div className={styles.message}>
          {message}
        </div>
        <div className={styles.formContainer}>
          <div className={styles.formGroup}>
            <div className={styles.inputContainer}>
              <label htmlFor="name">Nazwa</label>
              <Input
                id={'name'}
                placeholder={'Nazwa'}
                label={'Nazwa'}
                type="text"
                defaultValue={defaultName}
                error={errors.name}
                {...register("name", { ...validation.name })}
              />
              {nameErrors(errors.name?.type)}
            </div>
            <div className={styles.inputContainer}>
              <label htmlFor="quantity">Stan</label>
              <Input
                id={'quantity'}
                placeholder={'Stan'}
                label={'Stan'}
                type="number"
                defaultValue={defaultQuantity}
                minValue={0}
                error={errors.quantity}
                {...register("quantity", { ...validation.quantity })}
              />
              {quantityErrors(errors.quantity?.type)}
            </div>
            <div className={styles.inputContainer}>
              <label htmlFor="price">Cena</label>
              <Input
                id={'price'}
                placeholder={'Cena'}
                label={'Cena'}
                type="number"
                step="0.01"
                defaultValue={defaultPrice}
                minValue={0}
                error={errors.price}
                {...register("price", { ...validation.price })}
              />
              {priceErrors(errors.price?.type)}
            </div>
          </div>
          <div className={styles.formGroup}>
            <div className={styles.inputContainer}>
              <label htmlFor="unit">Jednostka</label>
              <select {...register('unit')} className={styles.select} defaultValue={defaultUnit}>
                <option value="szt">szt</option>
                <option value="m2">m2</option>
                <option value="mb">mb</option>
                <option value="ark">ark</option>
                <option value="ryz">ryz</option>
                <option value="litry">litry</option>
                <option value="inne">inne</option>
              </select>
            </div>
            <div className={styles.inputContainer}>
              <div className={styles.selectGroup}>
                <input
                  type="checkbox"
                  checked={categorySwitch}
                  onChange={() => setCategorySwitch(categorySwitch => !categorySwitch)} />
                <label htmlFor="category">Dodaj now kategori</label>
              </div>
              <Input
                id={'category'}
                placeholder={'Dodaj kategori'}
                label={'Kategoria'}
                type="text"
                defaultValue={defaultCategory}
                error={errors.category}
                disabled={!categorySwitch}
                {...register("category", { ...validation.category })}
              />
              {categoryErrors(errors.category?.type)}
            </div>
            <div className={styles.inputContainer}>
              <div className={styles.selectGroup}>
                <input
                  type="checkbox"
                  checked={!categorySwitch}
                  onChange={() => setCategorySwitch(categorySwitch => !categorySwitch)} />
                <label htmlFor="category">Wybierz kategori</label>
              </div>
              {
                categoryList ? (
                  <select
                    {...(!categorySwitch && { ...register('category') })}
                    className={styles.select}
                    disabled={categorySwitch}
                    defaultValue={defaultCategory}>
                    {
                      Object.entries(categoryList).map(([category]) => (
                        <option key={category} value={category}>{category}</option>
                      ))
                    }
                  </select>
                ) : null
              }
            </div>
          </div>
        </div>
        <div className={styles.actionForm}>
          <Button
            title={'Zapisz'}
            type={'submit'}
            style={{ width: '100%', padding: '0.8rem' }} />
        </div>
      </form>
    </div>
  )
}

export default ProductForm