import { Link } from 'react-router-dom'
import styles from './styles.module.scss'
import { useAuth } from 'hooks/useAuth'
import { useForm } from 'react-hook-form';
import { User } from 'models/user'
import Loading from 'components/common/Loading/Loading'
import Input from 'components/common/Input/Input'
import ErrorMessage from 'components/common/Messages/ValidateMessage';
import SuccessMessage from 'components/common/Messages/SuccessMessage'

const validation = {
  name: {
    required: true,
    maxLength: 20,
    minLength: 2,
  },
  email: {
    required: true,
    pattern: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  },
  password: {
    required: true,
    pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}$/
  }
}

const SignUp: React.FC = () => {
  const { loading, success: successResponse, signUp } = useAuth()

  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm<User>();

  const nameErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Imi jest wymagane'} />
      case 'minLength':
        return <ErrorMessage message={'Imi musi zawiera conajmiej 2 znaki'} />
      case 'maxLength':
        return <ErrorMessage message={'Imi moe zawiera maksymalnie 20 znakw'} />
      default:
        return null
    }
  }

  const emailErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Email jest wymagany'} />
      case 'pattern':
        return <ErrorMessage message={'Email jest niepoprawny'} />
      default:
        return null
    }
  }

  const passwordErrors = (type: any) => {
    switch (type) {
      case 'required':
        return <ErrorMessage message={'Haso jest wymagane'} />
      case 'pattern':
        return <ErrorMessage message={'Haso musi zawiera: co najmniej 8 znakw, 1 cyfr, 1 ma liter i 1 wielk liter'} />
      default:
        return null
    }
  }

  return (
    <div className={styles.auth}>
      <div className={styles.formContainer}>
        <div className={styles.left}>
          <h1>Zaloguj si</h1><br />
          <Link to='/logowanie'>
            <button type='button' className={styles.whiteBtn}>
              Logowanie
            </button>
          </Link>
        </div>
        <div className={styles.right}>
          {loading ? <Loading /> : null}
          {successResponse && <SuccessMessage message={'Zostae zarejestrowany'} />}
          <form className={styles.form} onSubmit={handleSubmit(signUp)}>
            <h1>Utwrz konto</h1>
            <Input
              id={'name'}
              placeholder={'Imi'}
              label={'Imi'}
              type="text"
              error={errors.name}
              {...register("name", { ...validation.name })}
            />
            {nameErrors(errors.name?.type)}
            <Input
              id={'email'}
              placeholder={'email'}
              label={'Email'}
              type="text"
              {...register("email", { ...validation.email })}
            />
            {emailErrors(errors.email?.type)}
            <Input
              id={'password'}
              placeholder={'password'}
              label={'Password'}
              type="password"
              {...register("password", { ...validation.password })}
            />
            {passwordErrors(errors.password?.type)}
            <Input
              id={'confirm_password'}
              placeholder={'confirm password'}
              label={'Confirm Password'}
              type="password"
              {...register("confirm_password", {
                required: true, validate: (val: string) => val === watch('password')
              })}
            />
            {errors.confirm_password && <ErrorMessage message={'Wpisz poprawne haso'} />}
            <button type='submit' className={styles.blueBtn}>
              Rejestracja
            </button>
          </form>
        </div>
      </div>
    </div>
  )
}

export default SignUp
